.App {
    display: grid;
    place-items: center;
    background-color: #dadbd3;
    height: 100vh;
}

.app__body {
    display: flex;
    background-color: #ededed;
    height: 90vh;
    width: 90vw;
    margin-top: -50px;
    box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.7);

    @media (max-width: 768px) {
        display: none !important;
    }
}